/* swiper
------------------------------------------------------ */
@use "../global/"as *;
@use "../base/setting"as *;

.swiper-container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.swiper {
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: 39px;
}

//スライダーコントロールを下部に表示させるための余白を設置する場合
.swiper-wrapper {
  width: 100%;
  height: 100%;
}

.swiper-button-next,
.swiper-button-prev {
  width: 20px;
  height: 40px;

  @include mq(md) {
    width: 20px;
    height: 25px;
  }
}



.swiper-button-prev:after,
.swiper-button-next:after {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: rem(32);
  color: $c_key1;

  @include mq(md) {
    font-size: rem(32);
  }
}

.swiper-button-prev:after {
  content: "\f104";
}

.swiper-button-next:after {
  content: "\f105";
}

.swiper-pagination-bullet-active {
  background: $c_key1;
}

.swiper-pagination-bullets {
  bottom: 0;
  text-align: right;
}