/* pager
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global" as *;

.pager {
  display: flex;
  overflow-x: auto;

  &__inner {
    display: flex;
    margin-right: auto;
    margin-left: auto;
  }

  &__inner > *:last-child {
    margin-right: 0;
  }

  &__item {
    list-style: none;
    margin-right: 10px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    color: $c_white;
    text-decoration: none;
    transition: .25s;
    background-image: $btn-gradient;
    font-size: rem(20);
    font-weight: bold;
  }

  &__link--active {
    background: #f1ebf8;
    color: $c_text;
    pointer-events: none;
  }
}