/* mv
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.mv {
  background-image: url(../../images/common/hero.png);
  background-size: cover;
  background-position: center;
  height: 100vh;
  color: $c_white;
  @include flex-center;
}

.mv__inner {
  padding: 0 30px;
  text-align: left;

  @include mq(md) {
    text-align: center;
  }
}

.mv__title-main {
  font-size: rem(40);
  line-height: 1.1;
  margin-bottom: 20px;

  @include mq(md) {
    font-size: rem(72);
  }
}

.mv__title-sub {
  font-size: rem(16);
  margin-bottom: 30px;
  letter-spacing: 0.1em;

  @include mq(md) {
    font-size: rem(24);
    margin-bottom: 50px;
  }
}

.mv__title-sub-br {
  @include mq(md) {
    display: none;
  }
}

.mv__scrolldown {
  position: absolute;
  right: 3px;
  bottom: 115px;
  color: #fff;
  letter-spacing: 0.2em;
  line-height: 1;
  text-transform: uppercase;
  font-size: rem(12);
}

.mv__scrolldown-text {
  transform: rotate(90deg);
  display: inline-block;
}

.mv__scrolldown::after {
  content: "";
  position: absolute;
  bottom: -115px;
  left: 50%;
  width: 1px;
  height: 80px;
  background-color: $c_white;
  -webkit-animation: scrollDownLine 1.5s infinite;
  animation: scrollDownLine 1.5s infinite;
}

@-webkit-keyframes scrollDownLine {
  0% {
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
  }
  50% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
  }
  50.1% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
  100% {
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
}
@keyframes scrollDownLine {
  0% {
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
  }
  50% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
  }
  50.1% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
  100% {
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
  }
}