/* breadcrumb
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.breadcrumb {
  background-color: $c_gray1;
  position: sticky;
  top: 64px;
}

.breadcrumb__list {
  max-width: $width;
  margin: 0 auto;
  padding: 5px 15px;

  @include mq(md) {
    padding: 10px 15px;
  }
}

.breadcrumb__item {
  display: inline;
  text-transform: uppercase;
  font-size: rem(11);

  @include mq(md) {
    font-size: rem(12);
  }
}

.breadcrumb__item:not(:last-child)::after {
  content: ">";
  margin: 0 .5em
}