/* recruit
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.recruit {
  padding: 0;
  @include mq(md) {
    padding: 70px 0;;
  }
}

.recruit__inner {
  @include mq(md) {
    display: flex;
    flex-direction: row-reverse;
  }
}

.recruit__img-inner {
  height: 200px;
  width: 100%;

  @include mq(md) {
    flex: 0 1 100%;
    height: 100%;
  }
}

.recruit__img {
  width: 100%;
  height: 100%;
}


.recruit__body {
  background-color: $c_gray1;
  position: relative;
  padding: 70px 40px;

  @include mq(md) {
    flex: 0 0 445px;
  }
}

.recruit__body-inner {
  text-align: center;
  @include mq(md) {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  }
}

.recruit__text {
  margin-bottom: 30px;
}